//font

@import url("https://fonts.googleapis.com/css2?family=Nunito+Sans:opsz,wght@6..12,200;6..12,300;6..12,400;6..12,500;6..12,600;6..12,700;6..12,800;6..12,900;6..12,1000&display=swap");

/* //colors variety import from  separate color stylesheet
@import "/css/ColorsVariety.css";
 */
$white: #f2f2f2;
$red-500: #cd3737;
$red-700: #b61a1a;
$grey-500: #dcdcdc;
$grey-600: #8d8c8c;
$grey-700: #3e3c3c;
$black-900: #120f10;
$black-700: #1c1b1b;
$black-600: #120f10;
$eastern-blue-500: #26acac;
$amulet-green-400: #85ab7b;
$ming-blue-600: #367983;

//mini- albums shadows and more
$shadow-flashback-normal: rgba(211, 142, 122, 0.5);
$color-flashback-normal: rgba(235, 152, 120, 1);

$shadow-flashback-deluxe: rgba(210, 146, 164, 0.5);
$color-flashback-deluxe: rgba(210, 146, 164, 1);

$shadow-arena-of-autumnn: rgba(185, 169, 71, 0.5);
$color-arena-of-autumnn: rgba(185, 169, 71, 1);

$shadow-galaktkitty: rgba(50, 23, 78, 0.5);
$color-galaktkitty: rgba(175, 160, 212, 1);

$shadow-king-of-for-and-muds: rgba(181, 133, 94, 0.5);
$color-king-of-for-and-muds: rgba(181, 133, 94, 1);

//other shadows
$shadow-green: rgba(133, 171, 123, 0.4);
$shadow-black: rgba(30, 29, 29, 0.6);
$shadow-black-transparent: rgba(0, 0, 0, 0.8);

* {
  margin: 0;
  box-sizing: border-box;
}

//styling

html {
  font-family: "Nunito Sans", sans-serif;
  font-size: 16px;
  overflow-x: hidden;
  background-color: $black-900;
  color: $white;
}

#background-album-cover {
  position: absolute;
  width: 100vw;
  height: 40rem;
  filter: blur(2rem);
  z-index: -2;
  & img {
    position: absolute;
    width: 100%;
    height: 100%;
    filter: blur(2rem);
    opacity: 0.6;
    object-fit: cover;
    top: 0;
    left: 0;
  }
}
//return link arrow

.arrow-link {
  display: flex;
  justify-content: center;
  align-items: center;
  text-decoration: none;
  transition: 0.1s ease-in-out;
  width: 1.75rem;
  height: 1.75rem;
  /* margin: 0.5rem 0; */
  padding: 1rem;
}

.arrow {
  display: flex;
  justify-content: center;
  align-items: center;
  border-radius: 50%;
  background-color: $shadow-black;
  width: 1.75rem;
  height: 1.75rem;
  /* margin: 0.5rem 0; */
  padding: 1rem;
}

#arrow-icon {
  color: $white;
  font-size: 1rem;
  transition: 0.2s ease-in-out;
}

.arrow-link:hover .arrow {
  background-color: $black-700;
}

.arrow-link:hover #arrow-icon,
.arrow-link:focus #arrow-icon {
  transform: scale(1.1);
  color: $grey-500;
}

//!for mobile menu and hide desktop menu
.top-content {
  display: flex;
  justify-content: space-between;
  align-items: center;
  background-color: $shadow-black-transparent;
  backdrop-filter: blur(0.5rem);
  width: 100%;
  height: 3.5rem;
  padding: 0 1rem;
  position: fixed;
  z-index: 2;
}
#logo,
#dropdown-menu-icon {
  display: block;
  width: 1.75rem;
  height: 1.75rem;
  font-size: 1.75rem;
  transition: ease-in-out 0.2s;
  cursor: pointer;
  &:hover {
    color: $grey-500;
  }
}
#chevron-to-show-social-menu {
  display: none;
}
.nav-desktop {
  display: none;
}

//toggle menu

.toggle-menu {
  display: none;
  position: fixed;
  width: 100%;
  height: auto;
  background-color: $black-600;
  transform: translateY(3.5rem);
  color: $grey-500;

  border-radius: 0 0 5% 5%;
  box-shadow: 1px 3px 15px $shadow-black-transparent;
  opacity: 0;
}

.toggle-menu.active {
  transition: 0.2s ease-in-out;
  opacity: 1;
  display: block;
  z-index: 2;
}

.all-inside {
  display: flex;
  justify-content: space-between;
  align-items: flex-start;
  flex-direction: row;
  padding: 1rem;
}

.toggle_menu-a {
  display: flex;
  justify-content: flex-start;
  align-items: flex-start;
  flex-direction: column;
  gap: 1rem;
  padding: 1rem 2rem;
  font-size: 1.2rem;
  text-transform: uppercase;
  font-weight: 800;
}
.nav-a-toggle-menu {
  text-decoration: none;
  color: $amulet-green-400;
  transition: 0.1s ease-in-out;
  &:hover {
    /* text-decoration: underline 0.2rem $eastern-blue-500; */
    transform: scale(1.05);
    color: $grey-600;
  }
}

//right side toggle menu
.toggle-column-right {
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
  width: auto;
  gap: 0.5rem;
}

#toggle-menu-a-1,
#toggle-menu-a-2,
#toggle-menu-a-3,
#toggle-menu-a-4,
#toggle-menu-a-5,
#toggle-menu-a-6,
#toggle-menu-a-7,
#toggle-menu-a-8,
#toggle-menu-a-9 {
  color: $white;
  font-size: 1.75rem;
  transition: 0.1s ease-in-out;

  &:hover {
    transform: scale(1.2);
    color: $grey-500;
  }
}

/* bottom navigation */
.bottom-navigation-only {
  height: 49.8%;
  width: 100vw;
}

.bottom-nav-ul {
  display: flex;
  justify-content: left;
  align-items: center;
  flex-direction: row;
  list-style: none;
  font-size: 0.9rem;
  padding: 0 1.5rem;
  width: 100vw;
  height: auto;
}

.bottom-li-nav {
  margin: 1rem;
  gap: 1.5rem;
}

#bottom-li-nav-about,
#bottom-li-nav-jobs,
#bottom-li-nav-appren {
  cursor: pointer;
}

#bottom-li-nav-about {
  color: $amulet-green-400;
}
.nav-a-social {
  display: none;
}
//section 1

.section1 {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: flex-start;
  width: 100%;
  height: auto;
  padding: 4rem 1rem 0 1rem;
}
#main-album-cover {
  display: flex;
  width: 100%;
  height: auto;
  box-shadow: 0px 2px 8px 1px $shadow-black;
  border-radius: 5%;
  max-width: 23rem; //!
}

#fig-album-released {
  padding: 0.5rem 0; //!

  position: relative;
  font-size: 0.8rem;
  font-weight: 500;
  color: $white;
  letter-spacing: 0.04rem;
}
.title-name-artist {
  padding-top: 1.5rem;
  width: 100%;
}
.album-name {
  font-size: 1.6rem;
  font-weight: 800;
  padding-bottom: 0.5rem;
  width: 100%;
}

.album-artist-name {
  padding-top: 0.5rem;
  height: auto;
  font-size: 1.2rem;
  font-weight: 600;
}

.album-summary-description {
  display: list-item;
  color: $white;
  cursor: pointer;
  padding: 0.5rem;
  background-color: transparent;
  width: 100%;
  text-transform: uppercase;
}

.focused {
  background-color: $grey-500;
  color: $ming-blue-600;
  box-shadow: 0 0 4px $shadow-black;
}

.album-description {
  line-height: 1.6rem;
  height: auto;
  padding-top: 1rem;
  font-size: 0.8rem;
  font-weight: 500;
  text-transform: uppercase;
}

.name-artist-in-p {
  color: $amulet-green-400;
}

.p-in-details {
  padding: 1rem;
  border-radius: 1%;
  background-color: $black-600;
  box-shadow: $shadow-black;
}

// section 2
.section2 {
  display: flex;
  flex-direction: column;
  width: 100%;
  height: auto;
  padding: 2rem 1rem;
}
#h3-section2 {
  font-size: 1.4rem;
  text-transform: uppercase;
  width: 100%;
  padding-bottom: 2rem;
  font-weight: 800;
}
.choose-player {
  display: flex;
  width: 100%;
  height: auto;
  background-color: transparent;
  opacity: 0.8;
}

.music-app-choose {
  display: flex;
  justify-content: center;
  align-items: center;
  width: 30%;
}

#box-1,
#box-2,
#box-3,
#box-4,
#box-5 {
  color: $white;
  padding: 0;
  font-size: 1.5rem;
  transition: 0.1s ease-in-out;
  cursor: pointer;
  &:active {
    color: $amulet-green-400;
    transform: scale(1.3);
  }
  &:hover {
    color: $amulet-green-400;
    transform: scale(1.3);
  }
}

//section 3 tracklist

.section3 {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: flex-start;
  width: 100%;
  height: auto;
  padding: 2rem 1rem;
}
.list-time-songs {
  display: flex;
  align-items: center;
  justify-content: flex-start;
  flex-direction: row;
  width: 100%;
  height: auto;
  padding: 1rem;
  gap: 1rem;
}

//! The big btn for starting to listen to music at the first one with an animation after click

.play-btn {
  display: flex;
  justify-content: center;
  align-items: center;
  background-color: $amulet-green-400;
  min-width: 3rem;
  min-height: 3rem;
  border-radius: 20%;
  border: 0;
  opacity: 1;
  transition: 0.2s ease-in;
  cursor: pointer;
  & {
    .load-song-big-icon {
      display: none;
    }
  }
  &:hover {
    opacity: 0.5;
    border-radius: 28%;
    .play-song-big-icon {
      color: $white;
    }
  }
  &:focus {
    .play-song-big-icon {
      display: none;
    }
    .load-song-big-icon {
      font-size: 1.2rem;
      color: $white;
      display: flex;
      animation: loading 1.5s linear forwards;
    }
  }
  &.ended-play {
    opacity: 1;
    border-radius: 20%;

    &:focus {
      .play-song-big-icon {
        display: block;
        color: $black-900;
      }
      .load-song-big-icon {
        display: none;
      }
    }
  }
}

//visible but hidden after click btn
.play-song-big-icon {
  font-size: 1.4rem;
}

//hidden but visible after click btn
.load-song-big-icon {
  font-size: 1.4rem;
}
#h4-section3 {
  font-size: 1.4rem;
  text-transform: uppercase;
  font-weight: 700;
}
.quantity-of-songs-and-time {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  font-size: 0.6rem;
  text-transform: uppercase;
  font-weight: 400;
  width: 40%;
}
.bxs-circle {
  font-size: 0.6rem;
  color: $eastern-blue-500;
}
#hr-line2 {
  border: none;
  width: 100%;
  background-color: $grey-700;
  height: 0.05rem;
  opacity: 0.1;
  margin: 0.5rem 0;
}

//only tracks test

//only left side song
.l_side {
  display: flex;
  align-items: center;
  justify-content: center;
  /* gap: 0.5rem; */
}

.song-number {
  display: flex;
  justify-content: center;
  align-items: center;
  font-size: 0.8rem;
  width: 1.2rem;
  font-weight: 600;
}
.song {
  display: flex;
  justify-content: space-between;
  align-items: center;
  width: 100%;
  height: 3rem;
  margin-bottom: 1rem;
  background-color: transparent;
  cursor: pointer;
  opacity: 1;
  transition: 0.3s linear;
  padding-right: 0.5rem;

  & .load-song-small2-icon {
    display: none;
  }
}

//! if is clicked song Div this 'animations' is add & after song is removed
.song-clicked {
  cursor: pointer;
  box-shadow: inset 1.5rem 0 1px 0px $ming-blue-600;
  color: $white;
  & {
    .load-song-small2-icon {
      display: none;
    }
  }
  & .song-title,
  .song-duration {
    opacity: 0.5;
  }
  & .artist-name {
    color: $amulet-green-400;
  }
  & .play-song-icon {
    display: none;
  }
  .load-song-small2-icon {
    display: block;
    font-size: 1.2rem;
    color: $grey-500;

    animation: loading 3s linear infinite;
  }
}

//! small btn for any song  with animation after click
.play-btn-song {
  padding: 0;
  display: flex;
  justify-content: center;
  align-items: center;
  background-color: transparent;
  width: 2.5rem;
  height: 2.5rem;
  border: 0;
  opacity: 1;
  transition: 0.2s ease-in;
  color: $eastern-blue-500;
  font-size: 1.5rem;
  cursor: pointer;
  & {
    .load-song-small-icon {
      display: none;
    }
  }
  &:hover {
    opacity: 0.5;
    .play-song-icon {
      color: $white;
    }
  }
  &:focus {
    .play-song-icon {
      display: none;
    }
    .load-song-small2-icon {
      display: none;
    }
    .load-song-small-icon {
      font-size: 1.2rem;
      color: $grey-500;
      display: block;
      animation: loading 3s linear infinite;
    }
  }
  &.active {
    border-radius: 0.5rem;
    background-color: 0.1rem solid $eastern-blue-500;
  }
}

.song-title {
  font-weight: 600;
  font-size: 1rem;
}
.artist-name {
  font-weight: 400;
  font-size: 0.8rem;
  color: $grey-600;
}

// only right side song

//! add display itp zeby by≈Ço columnowo te odliczanie
.song-duration {
  font-size: 0.8rem;
}

.anchor-dots {
  color: $ming-blue-600;
  font-size: 1.4rem;
  transition: 0.3s ease-in;
  z-index: 1;
  cursor: pointer;
  &:hover {
    transform: scale(1.1) rotate(1turn);
    color: $eastern-blue-500;
  }
}

.hidden-dots {
  color: $ming-blue-600;
  font-size: 1.4rem;
  transition: 0.3s ease-in;
  z-index: 1;
  padding-left: 0.2rem;
  cursor: pointer;
  transform: scale(1.1);
  &:hover {
    transform: scale(1.1);
    color: $eastern-blue-500;
  }
}

.r_side {
  display: flex;
  justify-content: center;
  align-items: center;
  width: 20%;
  gap: 0.5rem;
}
.visible-info {
  display: flex;
  justify-content: center;
  align-items: center;
  cursor: auto;
  /* width: auto; */
  min-width: 20%;
}

.hidden-info {
  display: none;
  //moze to ponizej usunac
  justify-content: center;
  align-items: center;
}
//heart click in song info

/* .heart-in-song */
.heart-btn-in-song {
  display: inline-block;
  font-size: 1.3rem;
  cursor: pointer;
  color: $grey-600;
  transition: 0.2s ease-in-out;
  z-index: 1;
  &:active {
    animation: heart-click 0.2s ease-in-out;
    color: $red-500;
  }
  &.is-red {
    color: $red-700;
  }
}

// btn for icons in song in right side
.btn-3-dots-visible,
.btn-3-dots-hidden,
.heart-btn-in-song {
  background-color: transparent;
  border: none;
  cursor: pointer;
  padding: 0;
  z-index: 1;
}

//section 4 other albums might you like

.section4 {
  display: flex;
  flex-direction: column;
  padding: 0 1rem 4rem 1rem;
  gap: 0.5rem;
}
#h5-section4 {
  font-size: 1.4rem;
  text-transform: uppercase;
  font-weight: 700;
  padding-bottom: 1rem;
}

.name-tag {
  color: $eastern-blue-500;
}

.albums-chooser {
  display: flex;
  flex-direction: row;
  justify-content: center;
  align-items: flex-start;
  flex-wrap: wrap;
  width: 100%;
  height: auto;
  min-height: 20rem;
  gap: 2rem;
  z-index: 1;
}

//only album
.album-cover {
  width: 14rem;
  height: auto;
  min-height: 14rem;
  min-width: 14rem;
