}

.content-small {
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: flex-start;
  gap: 0.5rem;
  width: 100%;
  height: auto;
  margin-top: auto;
}

.date-small {
  font-weight: 400;
  font-size: 1.1rem;
}

.dots-separator-album {
  color: $eastern-blue-500;
  font-size: 1.7rem;
}
// a - for mini albums
.album-a-next-page {
  text-decoration: none;
  color: $white;
}
// hover for album covers and dots separators
.album {
  .album-cover {
    transition: box-shadow 0.3s ease;
  }

  //1 album flashback deluxe
  &:hover #album-cover-flashback-deluxe {
    box-shadow: 0 0 8px $shadow-flashback-deluxe;
  }
  &:hover #dots-separator-album-1 {
    transition-delay: 300ms;
    color: $color-flashback-deluxe;
  }

  // 2 album arena of autumnn
  &:hover #album-cover-arena-of-autumnn {
    box-shadow: 0 0 8px $shadow-arena-of-autumnn;
  }
  &:hover #dots-separator-album-2 {
    transition-delay: 300ms;
    color: $color-arena-of-autumnn;
  }

  // 3 album galaktkitty
  &:hover #album-cover-galaktkitty {
    box-shadow: 0 0 8px $shadow-galaktkitty;
  }
  &:hover #dots-separator-album-3 {
    transition-delay: 300ms;
    color: $color-galaktkitty;
  }

  //4 album king of
  &:hover #album-cover-king-of {
    box-shadow: 0 0 8px $shadow-king-of-for-and-muds;
  }
  &:hover #dots-separator-album-4 {
    transition-delay: 300ms;
    color: $color-king-of-for-and-muds;
  }
}

//btn
.btn-view-div {
