.footer-p-and-nav {
  display: flex;
  justify-content: space-between;
  align-items: flex-start;
  flex-direction: column;
  width: 100%;
  height: auto;
  gap: 1rem;
  padding-top: 1rem;
}

.nav-a-footer-social {
  padding: 0 0.5rem;
}

#bottom-footer-social-1,
#bottom-footer-social-2,
#bottom-footer-social-3,
#bottom-footer-social-4,
#bottom-footer-social-5,
#bottom-footer-social-6,
#bottom-footer-social-7,
#bottom-footer-social-8,
#bottom-footer-social-9 {
  font-size: 1.4rem;
  color: $grey-500;
  cursor: pointer;

  &:hover {
    transition: 0.1s ease-in-out;
    transform: scale(1.2);
    color: $amulet-green-400;
  }
}
.time-and-rights {
  font-size: 1rem;
  color: $grey-500;
  font-size: 600;
}
//about me
.about-me {
  display: flex;
  justify-content: flex-start;
  flex-direction: column;
  align-items: flex-start;
  width: 100%;
  height: auto;
  padding-top: 2rem;
}
#description-about-me {
  font-size: 1rem;
  width: 80%;
  padding-bottom: 1rem;
}
#anchor-description-about-me {
  text-decoration: none;
  color: $grey-500;
  font-weight: 800;
  transition: 0.1s ease-in-out;
  &:hover {
    color: $eastern-blue-500;
  }
}

#my-name-is {
  font-size: 1.5rem;
  padding: 0.5rem 0;
}
.my-social {
  font-size: 3rem;
  color: $ming-blue-600;
  transition: 0.1s ease-in-out;
  &:hover {
    color: $amulet-green-400;
  }
}
// animations
@keyframes loading {
  0% {
    transform: rotate(0deg);
  }
  50% {
    transform: rotate(180deg);
  }
  100% {
    transform: rotate(360deg);
  }
}
// Animation for heart hover focus in song
@keyframes heart-hover-focus {
  0% {
    transform: scale(1);
  }
  50% {
    transform: scale(1.2);
  }
  100% {
    transform: scale(1);
  }
}

// Animation for heart click in song
@keyframes heart-click {
  0% {
    transform: scale(1);
  }
  50% {
    transform: scale(0.8);
  }
  100% {
    transform: scale(1);
  }
}

